import WordRow from './components/WordRow';
import { checkGuess } from './utils/word-checker';
import React from 'react';
import NumericInput from 'react-numeric-input';
import './App.css';
import {words8} from './utils/8letter';
import {words7} from './utils/7letter';
import {words6} from './utils/6letter';
import {words5} from './utils/5letter';
import {words4} from './utils/4letter';

var initword = null;



function todaysword(len) {
    if (initword == null) {
      if (len === 8)
        initword = words8[Math.floor(Math.random() * words8.length)];
      else if (len === 7)
        initword = words7[Math.floor(Math.random() * words7.length)];
      else if (len === 6)
        initword = words6[Math.floor(Math.random() * words6.length)];
      else if (len === 5)
        initword = words5[Math.floor(Math.random() * words5.length)];
      else if (len === 4)
        initword = words4[Math.floor(Math.random() * words4.length)];
    }
    console.log('initword : ' + initword);
    return initword;
}

function App() {
  const WordOfTheDay = 'STORY'
  const [keyboardStatus, setKeyboardStatus] = React.useState(undefined);
  const [wordSize, setWordSize] = React.useState(5)
  const [guesses, setGuesses] = React.useState([])
  const [currentWord, setCurrentWord] = React.useState('');
  const [gameState, setGameState] = React.useState('running');

  const [finalWord, setFinalWord] = React.useState("+++++");
  const defword = todaysword(wordSize);

  const resetword = () => {
    window.location.reload(false);
  }

  const resetCount = (e) => {
    //setCurrentWord('');
    setFinalWord([...Array(e + 1)].join('+'));
    setWordSize(e);
    initword = null;
    setCurrentWord('');
    setGameState('running');
    setGuesses([]);
    todaysword(e);
  }

  const backspace = () => {
    setCurrentWord(prev => prev && prev.slice(0, -1));
  }

  const enter = () => {
    //defword = todaysword();
    
    if (currentWord.length == wordSize) {
      setGuesses(prev => [...prev, currentWord])
      setCurrentWord('');

      const result = checkGuess(currentWord, initword);
      if(
        Object.values(result).every(v => v === 'green') &&
        Object.values(result).length === wordSize
      ) {
        setGameState('won');
        setFinalWord(initword);
      } else if (guesses.length == (wordSize - 1)) {
        setGameState('lost')
        setFinalWord(initword);
      }
    }
  }

  const word = (letter) => {
    setCurrentWord(prev => prev.length == wordSize ? prev : prev + letter.toUpperCase())
  }

  const handleKeyDown = e => {
    let pressedKey = String(e.key)
    if (pressedKey === "Backspace") {
      backspace();
      return
    }

    if (pressedKey === "Enter") {
      enter();
      return
    }

    let found = pressedKey.match(/[a-z]/gi)
    if (!found || found.length > 1) {
      return
    } else {
      word(pressedKey)
    }
  }

  

  React.useEffect(() => {
    window.addEventListener('keyup', handleKeyDown);
    window.addEventListener('touchstart', handleKeyDown);
    return () => {
      window.removeEventListener('keyup', handleKeyDown);
      window.removeEventListener('touchend', handleKeyDown);
    };
  }, [currentWord]);

  

  return (

    <div className="App">
      <div className="App-answer">Today's word is {finalWord} / {wordSize} - {gameState}</div>
      <header className="App-header">
        {guesses.map(guess => (
          <WordRow len={wordSize} word={guess} result={checkGuess(guess, initword)} />
        ))}
        {gameState === 'running' && <WordRow len={wordSize} word={currentWord} />}
      </header>
      <div className="App-refresh">
        <NumericInput min={4} max={8} value={wordSize} 
          onChange={(e) => resetCount(e)}
          onKeyDown={(event) => event.preventDefault()} />
      </div>
      <div>
        <input className="App-refresh-button" type="submit" value="refresh" onClick={resetword} />
      </div>
    </div>
  );
}

export default App;
