import React from 'react';
import './WordRow.css'

function WordRow({ len, word = '', result}) {
  return (
    <div className='word-row'>
      <form>
      {Array.from(Array(len)).map((_, i) => (
        <span contenteditable="true" className={result && result[i]}>
          {word[i] || '_'}
        </span>
      ))}
      </form>
    </div>
  )
}

export default WordRow;
